			}
		},
		mounted() {
			this.bnAuto()
			this.padding = document.getElementById("h_f").offsetHeight;
			window.addEventListener("scroll", this.scrollFixed)
			//  元素高度  bn.offsetHeight
			//  滚动距离  document.documentElement.scrollTop
		},
		beforeUnmount() {
			window.removeEventListener("scroll", this.scrollFixed);
			clearInterval(this.banners.auto)
		},
		methods: {
			bnAuto() {
				this.banners.auto = setInterval(() => {
					if (this.banners.index < this.banners.list.length - 1) {
						this.banners.index += 1
					} else if (this.banners.index >= this.banners.list.length - 1) {
						this.banners.index = 0
					} else if (this.banners.index <= 0) {
						this.banners.index = this.banners.list.length - 1
					}
				}, this.banners.interval)
			},  //自动轮播
			
			bnChange(e) {
				if (e.path[0].nodeName === "LI" || e.path[0].nodeName === "li") {
					if (this.banners.noShake === false) {
						let i = e.path[0].getAttribute("index");
						if (parseInt(i) !== this.banners.index) {
							clearInterval(this.banners.auto);
							this.banners.noShake = true;
							this.banners.index = parseInt(i);
							setTimeout(() => {
								this.banners.noShake = false;
								this.bnAuto()
							}, 500)
						}
					}
				}
			},  //点击切换（事件委派）
			
			scrollFixed() {
				if (document.getElementById("banner")) {
					if (document.documentElement.scrollTop > document.getElementById("banner").offsetHeight) {
						this.fixed = true
					} else {
						this.fixed = false
					}
				}
			}
		},
		components: {}
	}
</script>

