<template>
	<router-view></router-view>
	<div id="nav">
		<router-link to="/">
			<img src="http://t.hk.uy/tKC" />
			<p>首页</p>
		</router-link>
		<router-link to="/find">
			<img src="http://t.hk.uy/tKG" />
			<p>发现</p>
		</router-link>
		<router-link to="/infomation">
			<img src="http://t.hk.uy/tKH" />
			<p>消息</p>
		</router-link>
		<router-link to="/personal">
			<img src="http://t.hk.uy/tKK" />
			<p>我的</p>
		</router-link>
	</div>
	<div id="toast" v-if="toast.show">{{toast.text}}</div>
</template>

<script>
	export default {
		data() {
			return {
				toast: {
					text: "TIP",  //弹窗内容
					show: false,  //控制显示和隐藏
					noShake: false  //防止频繁触发
				},
				logged: false
			}
		},
		methods: {
			showToast(text = "TIP", out = 2000) {
				if (this.toast.noShake === false) {
					out = Number(out);
					if (out.toString() === "NaN" || out < 1000) {
						out = 2000
					};  //检测形参是否合规，否则改为默认
					if (Number(out) > 10000) {
						out = 10000
					};  //检测是否大于10秒
					text = text.toString();
					if (text.length > 100) {
						text = text.substring(0,100)
					};  //检测内容是否在一百字以内
					this.toast.text = text;
					this.toast.show = true;
					this.toast.noShake = true;
					setTimeout(() => {
						this.toast.text = "TIP";
						this.toast.show = false;
						this.toast.noShake = false
					}, out + 750)
				}
			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
		list-style: none;
		outline: none;
	}
	a {
		text-decoration: none;
		color: inherit;
	}
	img {
		user-select: none;
	}
	button {
		cursor: pointer;
	}
	#app {
		font-family: Avenir, Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		min-height: calc(100vh - 60px);
	}
	#toast {
		pointer-events: none;
		color: white;
		background-color: rgba(0,0,0,0.65);
		font-size: 14px;
		line-height: 20px;
		min-height: 20px;
		border-radius: 8px;
		position: fixed;
		margin: auto;
		width: fit-content;
		max-width: 450px;
		bottom: 10%;
		left: 0;
		right: 0;
		padding: 10px 20px;
		animation: 0.75s toastShow;
	}
	@keyframes toastShow {
		from {
			opacity: 0;
			bottom: 0;
		} to {
			opacity: 1;
			bottom: 10%;
		}
	}
	#nav {
		position: fixed;
		overflow: hidden;
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		height: fit-content;
		width: fit-content;
		border-top-right-radius: 20px;
		border-bottom-right-radius: 20px;
		transition: 0.5s all;
	}
	#nav a {
		height: 50px;
		width: 100px;
		margin: 20px 0;
		position: relative;
		border-bottom-right-radius: 25px;
		border-top-right-radius: 25px;
		text-align: center;
		border: 1px rgba(0,0,0,0.25) solid;
		display: flex;
		flex-direction: row;
		transition: 0.5s all;
	}
	#nav:hover a {
		width: 125px;
	}
	#nav a:hover {
		width: 160px;
	}
	#nav img {
		display: block;
		width: 26px;
		height: 26px;
		margin: auto;
		margin-right: 5px;
	}
	#nav p {
		text-align: center;
		color: #666;
		margin: auto;
		margin-left: 0;
		font-size: 14px;
		font-weight: bold;
		transition: 0.75s all;
	}
	.router-link-active {
		background-color: rgba(128,128,128,0.25);
	}
	.router-link-active p {
		font-size: 16px !important;
		color: black !important;
	}
	@media screen and (max-width: 1000px) {
		#nav {
			top: auto;
			bottom: 0;
			display: flex;
			border-radius: 0;
			width: 100vw;
			background-color: white;
		}
		#nav::before {
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			position: absolute;
			top: 0;
			z-index: 1;
			background-color: rgba(0,0,0,0.1);
		}
		#nav a {
			flex-direction: column;
			margin: auto;
			border-radius: 0;
			flex: 1;
			border: none;
			height: 60px;
		}
		#nav:hover a {
			width: auto;
		}
		#nav a {
			width: auto;
		}
		#nav img {
			width: 24px;
			height: 24px;
			margin: auto;
			margin-bottom: 0;
		}
		#nav p {
			margin: auto;
			margin-top: 0;
			font-weight: normal;
		}
	}
	@media screen and (max-width: 500px) {
		#toast {
			max-width: 65%;
		}
		.router-link-active p {
			font-size: 14px !important;
			font-weight: bold !important;
		}
	}
</style>